    margin: 0;
    font-family: Arial, sans-serif;
    background: #1a1a1a;
    color: white;
  }
  
  .header {
    display: flex;
    justify-content: space-between;
    padding: 10px;
    background: #333;
  }
  
  .game-container {
    display: flex;
    justify-content: space-between;
    padding: 20px;
  }
  
  .graph {
    width: 70%;
    height: 400px;
    background: #222;
    border-radius: 10px;
    padding: 20px;
  }
  
  .betting-panel {
    width: 20%;
    background: #222;
    padding: 20px;
    border-radius: 10px;
  }
  
  .betting-controls input,
  .betting-controls button {
    display: block;
    width: 100%;
    margin: 10px 0;
    padding: 10px;
    font-size: 16px;
  }
  
  .player-list {
    width: 20%;
    background: #222;
    padding: 20px;
    border-radius: 10px;
  }
